body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: rgb(65, 62, 62);
}

/* code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
} */

div.auth {
    width: 320px;
    display: flex;
    margin: 210px auto 0;
    flex-direction: column;
    align-items: center;
}

form.auth {
    display: flex;
    flex-direction: column;
    align-items: center;
}

nav {
    background-color: hsl(0deg 0% 9%);
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    padding: 0 90px;
}

.link {
    margin-right: 2rem;
    color: white;
}

.main {
    display: flex;
    flex-direction: column;
    width: 400px;
    margin: 40px 100px;
    color: white;
}

.myloader {
    padding: 10px;
    align-items: center;
    justify-content: center;
    width: auto;
}

span.pt {
    margin-top: 20px;
}

span.logout {
    color: white;
    display: flex;
    align-items: center;
}

span.logout > span {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

span.logout > span > p {
    margin: 0;
}

span.logout > * {
    margin-left: 2rem;
}

span.logout > p:hover {
    color: aqua;
}

.numi {
    width: 45px;
}

.ch,
p {
    margin: 4px 0;
}

a {
    color: wheat;
}

.msel {
    color: black;
    margin: 5px 0;
}

.ssel {
    margin: 5px 0;
}

.loader {
    width: 30px;
    height: 30px;
    border: 4px solid #fff;
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.between {
    display: flex;
    justify-content: space-between;
    align-content: center;
    align-items: center;
    height: 30px;
    margin: 10px 0;
}

.column {
    flex-direction: column;
    height: 60px;
}
